.modal {
    display: none;
    width: 100%;
    height: 100%;
    overflow: auto;

    background-color: $modal-bg;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;

    &.show {
        display: flex;
    }

    &__dialog {
        width: 100%;
        max-width: 1200px;
        margin: auto;
        position: relative;
        background-color: $white;

        transform: scale(0);
        transition: transform .2s linear;
    }

    &__close {
        width: 34px;
        height: 39px;
        padding: 0;

        background: none;
        border: 0;
        cursor: pointer;

        position: absolute;
        top: .9rem;
        right: 1rem;
        z-index: 1;

        transition: transform .2s linear;

        &:hover {
            transform: translateY(-3px);
        }
    }

    &__content {
        padding: 5rem 1rem;
    }

    &__title {
        margin-bottom: 3.6rem;

        font-size: 3rem;
        text-transform: uppercase;
        color: $black;
        font-weight: 700;
    }
}

//Form style


//Currency dropbox
.input-group {
    display: flex;
    flex-direction: row;
}

.currency-amount {
    width: 90%;
    margin: 0 1rem;
  }

.currency-addon{
    display: flex;
    flex-direction: row;
    text-align: left;
    position: relative;
}

.header-text {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.form-header {
    padding-top: 2rem;
}

.anonimously {
    display: flex;
    flex-direction: column;
    align-items: center;

    & h2 {
